import SearchLink from '@/components/SearchLink'
export default {
  components: {
    SearchLink
  },
  data () {
    return {
      activeIndex: 0,
      categories: []
    }
  },
  created () {
    this.getCategories()
  },
  methods: {
		changeIndex(index){
			this.activeIndex = index
		},
    toSearchList (catName) {
      wx.navigateTo({ url: '/pages/search_list/main?query=' + catName })
    },
    getCategories () {
      this.$request({
        url: '/api/public/v1/categories'
      }).then(data => {
        // console.log(data)
        this.categories = data
      })
    }
  }
}
